import React from 'react';
import PopupWithForm from './PopupWithForm';
import registerSuccess from '../images/register_success.svg';
import registerFail from '../images/register_fail.svg';

export default function InfoToolTip(props){
  return (
    <PopupWithForm isOpen={props.isOpen} onClose={props.onClose}>
      {
        props.success ?
          (<figure className="popup__tooltip">
            <img src={registerSuccess} alt="–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è: —É—Å–ø–µ—Ö"/>
            <figcaption className="popup__title">–í—ã —É—Å–ø–µ—à–Ω–æ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–ª–∏—Å—å</figcaption>
          </figure>) :
          (<figure className="popup__tooltip">
            <img src={registerFail} alt="–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è: –æ—à–∏–±–∫–∞"/>
            <figcaption className="popup__title">–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫! –ü–æ–ø—Ä–æ–±—É–π—Ç–µ –µ—â—ë —Ä–∞–∑.</figcaption>
          </figure>)
      }
    </PopupWithForm>
  )
}