import React from 'react';
import {Link} from 'react-router-dom';

export default function Login(props) {
  const [email, setEmail] = React.useState('');
  const [password, setPassword] = React.useState('');

  const handleChange = event => {
    if (event.target.name === 'email') setEmail(event.target.value);
    if (event.target.name === 'password') setPassword(event.target.value)
  }

  const handleSubmit = event => {
    event.preventDefault();
    props.onRegister({ email, password });
    setEmail('');
    setPassword('');
  }

  return (
    <section className="login-register">
      <h2 className="login-register__title">–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è</h2>
      <form action="/" name={props.type} className="login-register__form" noValidate onSubmit={handleSubmit}>
        <input type="text"
          name="email"
          placeholder="Email"
          className="login-register__input"
          minLength="5"
          maxLength="40"
          required
          value={email}
          onChange={handleChange}/>
        <input type="password"
          name="password"
          placeholder="–ü–∞—Ä–æ–ª—å"
          className="login-register__input" 
          minLength="5"
          maxLength="200"
          required
          value={password}
          onChange={handleChange}/>
        <button type="submit" className="login-register__submit-btn">–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è</button>
        <p className="login-register__already-title">
          –£–∂–µ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–Ω—ã ? <Link to="/sign-in" className="login-register__already-login">–í–æ–π—Ç–∏</Link>
        </p>
      </form>
    </section>
  )
}