import {authURL} from './constants';

export class Auth{
  constructor(){
    this._headers = {
      'Content-Type': 'application/json'
    };
  }

  registerUser(userData){
    return fetch(`${authURL}/signup`, {method: 'POST', headers: this._headers, body: JSON.stringify(userData)})
      .then(res => {
        if (res.ok) {
          return res.json()
        }
        else {
          return res.status === 400 ?
            Promise.reject(`–û—à–∏–±–∫–∞ –≤ {registerUser}: –Ω–µ –ø–µ—Ä–µ–¥–∞–Ω–æ –æ–¥–Ω–æ –∏–∑ –ø–æ–ª–µ–π`) :
            Promise.reject(`–û—à–∏–±–∫–∞ –ø–æ–ª—É—á–µ–Ω–∏—è —Ä–µ–∑—É–ª—å—Ç–∞—Ç–∞ –≤ {registerUser}: ${res.status} ${res.statusText}`);
        }
      })
  }

  loginUser(userData){
    return fetch(`${authURL}/signin`, {method: 'POST', headers: this._headers, body: JSON.stringify(userData)})
      .then(res => {
        if (res.ok) {
          return res.json()
        }
        else {
          if (res.status === 400) {
            return Promise.reject(`–û—à–∏–±–∫–∞ –≤ {loginUser}: –Ω–µ –ø–µ—Ä–µ–¥–∞–Ω–æ –æ–¥–Ω–æ –∏–∑ –ø–æ–ª–µ–π`)
          }
          else if (res.status === 401) {
            return Promise.reject(`–û—à–∏–±–∫–∞ –≤ {loginUser}: –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å —Å ${userData.email} –Ω–µ –Ω–∞–π–¥–µ–Ω`)
          }
          else {
            return Promise.reject(`–û—à–∏–±–∫–∞ –ø–æ–ª—É—á–µ–Ω–∏—è —Ä–µ–∑—É–ª—å—Ç–∞—Ç–∞ –≤ {loginUser}: ${res.status} ${res.statusText}`);
          }
        }
      })
  }

  validateUser(jwt){
    return fetch(`${authURL}/users/me`, {method: 'GET', headers: {...this._headers, "Authorization": `Bearer ${jwt}`}})
    .then(res => {
      if (res.ok) {
        return res.json()
      }
      else {
        if (res.status === 400) {
          return Promise.reject(`–û—à–∏–±–∫–∞ –≤ {validateUser}: –¢–æ–∫–µ–Ω –Ω–µ –ø–µ—Ä–µ–¥–∞–Ω –∏–ª–∏ –ø–µ—Ä–µ–¥–∞–Ω –Ω–µ –≤ —Ç–æ–º —Ñ–æ—Ä–º–∞—Ç–µ`)
        }
        else if (res.status === 401) {
          return Promise.reject(`–û—à–∏–±–∫–∞ –≤ {validateUser}: –ü–µ—Ä–µ–¥–∞–Ω–Ω—ã–π —Ç–æ–∫–µ–Ω –Ω–µ–∫–æ—Ä—Ä–µ–∫—Ç–µ–Ω`)
        }
        else {
          return Promise.reject(`–û—à–∏–±–∫–∞ –ø–æ–ª—É—á–µ–Ω–∏—è —Ä–µ–∑—É–ª—å—Ç–∞—Ç–∞ –≤ {validateUser}: ${res.status} ${res.statusText}`);
        }
      }
    })
  }
}

const auth = new Auth();
export default auth;